@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap');
@import "minimal-mistakes/skins/dark"; // skin
@import "minimal-mistakes"; // main partials

html {
    font-size: 16px; // change to whatever
    
